<script>
  import { view, toastMessage, overlay } from "../store/store";
  import Toast from "./Toast.svelte";
  import { handleRouting } from "../util";

  function setView(viewToSet) {
    handleRouting(viewToSet);
    if (viewToSet === "entry") {
      overlay.set(viewToSet);
    } else {
      overlay.set("");
      view.set(viewToSet);
    }
  }
</script>

<style type="text/postcss">.navbar{box-shadow:0 0 1px 0 grey;height:56px;background-color:var(--navbar-color);width:100%;position:fixed;bottom:0;fill:currentColor}.nav-button,.navbar{display:flex;align-items:center}.nav-button{flex-direction:column;justify-content:center;width:50%;padding-top:.25rem;padding-bottom:.25rem;background-color:transparent;transition:background-color .25s ease-out}.navbar .active{color:hsl(var(--primary-hue),65%,45%);transition:all .18s;font-size:1rem}.navbar .inactive{transition:all .18s;color:#a0aec0;font-size:.875rem}.content{position:absolute;width:100%;top:0;overflow-x:hidden;bottom:56px;background-color:var(--background-color)}.entry-wrapper{position:fixed;bottom:0;width:100%;margin-bottom:1.5rem;pointer-events:none}#entry-button,.entry-wrapper{display:flex;align-items:center;justify-content:center}#entry-button{border-radius:9999px;width:4rem;height:4rem;padding:1rem;fill:currentColor;color:#fff;box-shadow:0 10px 15px -3px rgba(0,0,0,.1),0 4px 6px -2px rgba(0,0,0,.05);pointer-events:auto;background-color:hsl(var(--accent-hue),50%,50%);box-shadow:0 3px 6px 0 hsla(var(--accent-hue),35%,25%,.4)}#entry-button,#entry-button:hover{transition:background-color .25s ease-out}#entry-button:hover{background-color:hsl(var(--accent-hue),70%,55%)}#entry-button:active{background-color:hsl(var(--accent-hue),70%,45%);transition:background-color 80ms ease-out}.material-icons-round{margin-right:0}#entry-button .material-icons-round{font-size:32px}@media only screen and (min-width:768px){.content{bottom:0}.navbar{width:25%;height:100vh;display:flex;flex-direction:column;position:fixed;left:0;fill:currentColor;align-items:flex-start;justify-content:center}.nav-button{display:flex;flex-direction:row;align-items:center;justify-content:flex-start;width:100%;padding:1rem 1rem 1rem 3rem}.nav-button:hover{background-color:hsla(var(--secondary-hue),30%,85%,.2);transition:background-color .25s ease-out}.nav-button:active{background-color:hsla(var(--secondary-hue),30%,75%,.4);transition:background-color 80ms ease-in}button:nth-child(2){margin-top:2rem;margin-bottom:.5rem}.navbar .active{color:hsl(var(--primary-hue),65%,45%)}.navbar .active,.navbar .inactive{transition:color .18s;font-size:1.25rem}.navbar .inactive{color:#a0aec0}.material-icons-round{margin-right:.5rem;font-size:1.875rem}.content{position:absolute;width:75%;top:0;right:0;overflow-x:hidden;background-color:var(--background-color)}#entry-button{border-radius:9999px;width:83.33333%;display:flex;flex-direction:row;justify-content:center;align-items:center;fill:currentColor;color:#fff;font-weight:700;font-size:1.25rem;pointer-events:auto;background-color:hsl(var(--accent-hue),50%,50%);box-shadow:0 3px 6px 0 hsla(var(--accent-hue),35%,25%,.4)}}</style>

<div class="content">
  <slot />
  {#if $toastMessage !== ''}
    <Toast message={$toastMessage} />
  {/if}
</div>
<nav class="navbar">
  {#if window.innerWidth > 768}
    <div class="w-full flex items-center justify-center">
      <button id="entry-button" on:click={() => setView('entry')}>
        <i class="material-icons-round">post_add</i>
        Add spending
      </button>
    </div>
  {/if}
  <button
    class="nav-button {$view === 'dashboard' ? 'active' : 'inactive'}"
    on:click={() => setView('dashboard')}>
    <i class="material-icons-round">assessment</i>
    <span>Dashboard</span>
  </button>
  <button
    class="nav-button {$view === 'settings' ? 'active' : 'inactive'}"
    on:click={() => setView('settings')}>
    <i class="material-icons-round md-48">settings_applications</i>
    <span>Settings</span>
  </button>
</nav>
{#if window.innerWidth <= 768}
  <div class="entry-wrapper {$overlay.length > 0 ? '' : 'z-10'}">
    <button id="entry-button" on:click={() => setView('entry')}>
      <i class="material-icons-round md-36">post_add</i>
    </button>
  </div>
{/if}
