<script>
  import {
    view,
    toastMessage,
    userInfo,
    entryData,
    dashboardShouldReload,
    overlay,
    detailData,
    filteredSearchData
  } from "../store/store";
  import { onMount, onDestroy } from "svelte";
  import TypeButton from "./TypeButton.svelte";
  import LoadingSpinner from "./LoadingSpinner.svelte";
  import { typeDesigns, handleRouting, getDateString } from "../util";
  import { fly, fade } from "svelte/transition";
  import firebase from "firebase/app";
  import "firebase/firestore";

  let scrolling = false;

  let description = "";
  let amount = 0;
  let date = getDateString();
  let type = "Food";
  let suggestedDescriptions = [];

  let amountValid = false;
  let dateValid = false;
  let typeValid = false;

  let isUpdate = false;

  let wrapperEl;

  const commonDescriptions = new Map([
    ["Food", ["GrabFood", "Foodpanda", "Lunch at Westgate", "Lunch at JEM"]],
    ["Groceries", ["NTUC", "Redmart"]],
    ["Transport", ["Grabcar", "Taxi"]],
    ["Household", ["Cleaner", "Kid's clothes", "Diapers"]],
    ["Utilities", ["Internet", "Electricity", "Gas/Water"]]
  ]);

  onMount(() => {
    //Use existing data to be edited if user came from Detail view
    if (Object.keys($entryData).length > 0) {
      isUpdate = true;

      description = $entryData.desc;
      amount = $entryData.amount;
      date = $entryData.date;
      type = $entryData.type;

      document.getElementById("today-button").classList.remove("active");
      document.getElementById("yesterday-button").classList.remove("active");
    }

    wrapperEl = document.getElementById("entry-page");
    wrapperEl.addEventListener("scroll", ev => {
      ev.target.scrollTop > 0 ? (scrolling = true) : (scrolling = false);
    });

    //Remove higlight for Today and Yesterday date shortcut buttons since user wants to enter own date
    document.getElementById("date-input").oninput = () => {
      document.getElementById("today-button").classList.remove("active");
      document.getElementById("yesterday-button").classList.remove("active");
    };

    //Get focus on Amount input field
    document.getElementById("amount-input").focus();
    document.getElementById("amount-input").select();
  });

  onDestroy(() => {
    entryData.set({});
  });

  function setDate(dateToSet) {
    if (dateToSet === "today") {
      date = getDateString();
      document.getElementById("today-button").classList.add("active");
      document.getElementById("yesterday-button").classList.remove("active");
    } else if (dateToSet === "yesterday") {
      const yesterday = new Date(Date.now() - 86400000);
      const _year = yesterday.getFullYear();
      const _month = yesterday.getMonth() + 1;
      const _date = yesterday.getDate();

      date = getDateString(_year, _month, _date);
      document.getElementById("yesterday-button").classList.add("active");
      document.getElementById("today-button").classList.remove("active");
    }
  }

  function receiveType(e) {
    type = e.detail.selectedType;
  }

  function sendEntry() {
    handleRouting("dashboard");
    view.set("dashboard");
    overlay.set("");

    toastMessage.set("Submitting...");

    const db = firebase.firestore();
    const newId = Date.now().toString() + amount;

    db.collection("expenses")
      .doc($entryData.id ? $entryData.id : newId)
      .set({
        amount: amount,
        date: date,
        desc: description,
        type: type,
        addedBy: $userInfo.name,
        addedOn: getDateString(),
        id: $entryData.id ? $entryData.id : newId
      })
      .then(() => {
        toastMessage.set("Expense created!");
        setTimeout(() => toastMessage.set(""), 1000);
        dashboardShouldReload.set(true);
      })
      .catch(error => {
        toastMessage.set(error);
        setTimeout(() => toastMessage.set(""), 3000);
      });
  }

  function deleteEntry() {
    handleRouting("dashboard");
    overlay.set("");

    const db = firebase.firestore();
    db.collection("expenses")
      .doc($entryData.id)
      .delete()
      .then(() => {
        toastMessage.set("Entry deleted");
        setTimeout(() => toastMessage.set(""), 1000);
        dashboardShouldReload.set(true);
      })
      .catch(error => {
        toastMessage.set(error);
        setTimeout(() => toastMessage.set(""), 3000);
      });
  }

  $: if (amount <= 0 || amount === undefined) {
    amountValid = false;
  } else {
    amountValid = true;
  }

  $: if (date !== "") {
    dateValid = true;
  } else {
    dateValid = false;
  }

  $: if (type !== "") {
    typeValid = true;
    suggestedDescriptions = commonDescriptions.get(type);
  } else {
    typeValid = false;
  }
</script>

<style type="text/postcss">#entry-wrapper::-webkit-scrollbar{display:none}input{outline:none!important;padding:.25rem .5rem;margin-right:1rem;width:66.66667%;-webkit-appearance:none;-moz-appearance:none;appearance:none;text-align:right;font-weight:700;color:#718096;background-color:transparent}#date-input{width:auto}#description-input{font-size:1.125rem;font-weight:400}.label{font-size:1.5rem;font-weight:700;margin-left:1rem;margin-right:.5rem;width:33.33333%;color:var(--text-color)}.label-error:after{content:"‚Ä¢";position:relative;margin-left:.5rem;margin-bottom:1rem;color:#e53e3e}.input-row{display:flex;flex-direction:row;width:100%;justify-content:space-between;align-items:center;margin-top:3rem}.date-button{border-radius:9999px;padding:.25rem .75rem;font-weight:700;transition:background-color .35s;color:var(--text-color2);background-color:var(--inactive-button-color)}.date-button.active{background-color:hsl(var(--secondary-hue),50%,50%);color:#f5f5f5}.form-wrapper{display:flex;flex-direction:column;width:100%}.suggestion-button{padding:.25rem .75rem;align-self:flex-start;margin-right:.5rem;margin-top:.5rem;color:var(--text-color2);background-color:var(--inactive-button-color)}.submit-button,.suggestion-button{border-radius:9999px;transition:background-color .25s ease-out}.submit-button{padding:.5rem 1rem;color:#fff;font-size:1.5rem;font-weight:700;width:80%;margin-top:6rem;margin-bottom:2rem;background-color:#e2e8f0}.submit-button.active,.submit-button.inactive{background-color:hsl(var(--accent-hue),50%,50%)}.submit-button.inactive{opacity:.3}.submit-button:hover{background-color:hsl(var(--accent-hue),70%,55%);transition:background-color .25s ease-out}.submit-button:active{background-color:hsl(var(--accent-hue),70%,45%);transition:background-color 80ms ease-out}.date-button:hover,.suggestion-button:hover{background-color:hsla(var(--secondary-hue),30%,85%,.4);transition:background-color .25s ease-out}.date-button:active,.suggestion-button:hover{background-color:hsl(var(--secondary-hue),30%,85%,.4);transition:background-color .25s ease-out}.sign-out-button{color:#e53e3e;border-radius:9999px;padding:.5rem 1rem;margin-bottom:2rem;text-align:center;width:25%;background-color:transparent}.sign-out-button,.sign-out-button:hover{transition:background-color .25s ease-out}.sign-out-button:hover{background-color:hsla(var(--secondary-hue),30%,85%,.2)}.sign-out-button:active{background-color:hsla(var(--secondary-hue),30%,75%,.4);transition:background-color 80ms ease-in}@media only screen and (min-width:768px){.form-wrapper{width:50%}}</style>

<div
  id="entry-page"
  class="h-screen w-full absolute top-0 overflow-auto"
  style="background-color: var(--background-color); color: var(--text-color)"
  out:fade={{ duration: 80 }}>
  <div
    class="w-full flex flex-row p-4 {scrolling ? 'shadow' : ''} fixed top-0
    justify-between z-10"
    style="height: 56px; background-color: var(--background-color)">
    <button>
      <i
        class="material-icons-round fill-current"
        style="color: hsl(var(--primary-hue), 50%, 50%)"
        aria-label="Back button"
        on:click={() => {
          Object.keys($detailData).length > 0 ? window.history.go(-2) : window.history.back();
        }}>
        arrow_back
      </i>
    </button>
  </div>
  <div class="flex flex-col items-center justify-around mt-8">
    <div class="form-wrapper">
      <div class="input-row" in:fade={{ duration: 120, delay: 30 }}>
        <label
          for="amount-input"
          class={amountValid ? 'label' : 'label label-error'}>
          Amount
        </label>
        <input
          id="amount-input"
          class="amount text-2xl"
          type="number"
          min="0"
          bind:value={amount}
          on:click={() => document.execCommand('selectall', null, false)} />
      </div>
      <div class="input-row" in:fade={{ duration: 120, delay: 60 }}>
        <label
          for="date-input"
          class={dateValid ? 'label' : 'label label-error'}>
          Date
        </label>
        <div class="mr-4">
          <button
            id="today-button"
            class="date-button active"
            on:click={() => setDate('today')}>
            Today
          </button>
          <button
            id="yesterday-button"
            class="date-button"
            on:click={() => setDate('yesterday')}>
            Yesterday
          </button>
        </div>
      </div>
      <div
        class="flex flex-row w-full justify-between items-center mt-2 text-lg
        ml-4"
        style="color: var(--text-color2)"
        in:fade={{ duration: 80, delay: 90 }}>
        <span>Other date</span>
        <input id="date-input" type="date" bind:value={date} />
      </div>
      <div class="mt-12 flex flex-col" in:fade={{ duration: 120, delay: 150 }}>
        <label class="label">Type</label>
        <div class="w-full flex flex-row flex-wrap justify-start">
          {#each typeDesigns as typeDesign}
            <TypeButton
              label={typeDesign.type}
              colorHue={typeDesign.hue}
              on:dispatchType={receiveType}
              isActive={type === typeDesign.type}
              materialIcon={typeDesign.materialIcon} />
          {/each}
        </div>
      </div>
      <div class="input-row" in:fade={{ duration: 120, delay: 120 }}>
        <label for="description-input" class="label">Description</label>
        <input
          class="truncate text-2xl"
          id="description-input"
          type="text"
          placeholder="(Optional)"
          bind:value={description}
          on:click={() => document.execCommand('selectall', null, false)} />
      </div>
      <div class="flex mt-4 mx-4 flex-wrap" style="height: 80px">
        {#if suggestedDescriptions}
          {#each suggestedDescriptions as suggestion, index (suggestion)}
            <button
              class="suggestion-button"
              in:fade={{ duration: 180, delay: 30 * index }}
              on:click={() => (description = suggestion)}>
              {suggestion}
            </button>
          {/each}
        {/if}
      </div>
      <div
        class="w-full text-center block"
        in:fade={{ duration: 120, delay: 180 }}>
        <button
          class="submit-button {typeValid && dateValid && amountValid ? 'active' : 'inactive cursor-not-allowed'}"
          disabled={!(typeValid && dateValid && amountValid)}
          on:click={() => sendEntry()}>
          {isUpdate ? 'Update' : 'Submit'}
        </button>
      </div>
      {#if Object.keys($entryData).length > 0}
        <div class="w-full text-center">
          <button class="sign-out-button" on:click={() => deleteEntry()}>
            Delete
          </button>
        </div>
      {/if}
    </div>
  </div>
</div>
